.other_card {
  background: #141414;
  border-radius: 8px;
  color: #ffffff;
  padding: 2rem;
}
.other_card ul li {
  list-style: none;
  font-family: "Axiforma Regular";
  display: flex;
  align-items: center;
}
.other_card ul li::before {
  content: "\2022";
  color: rgba(255, 243, 209, 0.2);
  margin-right: 1rem;
  font-size: 1.5rem;
}
