.home-top {
  height: 85vh;
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  margin: auto;
  text-align: center;
}

.home-top img {
  width: 30%;
  border-radius: 100%;
  object-fit: contain;
}

.home-top .home-top-about {
  color: rgba(129, 129, 129, 0.75);
}
.home-top .home-top-about span {
  color: #a3a3a3;
}
.go-to-selected-projects {
  color: rgba(129, 129, 129, 0.85);
  font-size: 1.5rem;
  margin-top: 3rem;
  text-align: center;
  cursor: pointer;
}
.go-to-selected-projects img {
  transform: rotate(180deg);
}

.selected-projects {
  width: 85%;
  margin: 4rem auto;
  display: grid;
  row-gap: 5rem;
}
/* home top content*/
.home-top-content {
  margin-top: 120px;
}
.home-top-content-inner {
  width: 85%;
  margin: auto;
  display: flex;
}
.home-top-content-inner-l {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-basis: 50%;
}
.home-top-content-inner-l-1 {
  display: flex;
  flex-direction: column;
  margin-left: 15%;
}
.home-top-content-inner-l-1-h {
  font-weight: bold;
  font-size: 30px;
}
.home-top-chat-link {
  margin-top: 40px;
}
.home-top-content-inner-l-2 {
  display: flex;
  width: 100%;
}
.home-top-content-inner-l-2-n {
  margin-right: 30px;
}
.home-top-content-inner-l-2-n p {
  font-weight: 550;
  font-size: 40px;
}
.home-top-content-inner-l-2-n div {
  margin-left: 5px;
}
.home-top-content-inner-r {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  flex-basis: 50%;
}
.home-top-content-inner-r img {
  width: 55%;
}

/* 
=========================
=========================
home-second-layer
*/
.home-second-layer {
  margin: auto;
  margin-top: 40px;
  margin-bottom: 20px;
  width: 85%;
  justify-content: center;
  align-items: center;
}
.home-second-layer-l,
.home-second-layer-r {
  flex-basis: 50%;
}
.home-second-layer-l-div {
  width: 90%;
  background-color: rgb(45, 45, 45);
  backdrop-filter: blur(20px);
  border-radius: 3px;
  padding: 5px 0px 0px 0px;
  margin-bottom: 10px;
}
.home-second-layer-l-div div {
  width: 90%;
  margin: auto;
}
.home-second-layer-r {
  display: flex;
  justify-content: flex-end;
}
.home-second-layer-r-div {
  width: 90%;
}
.about-name {
  font-weight: 600;
  font-size: 28px;
}

/* 
====================
====================
skillls
*/
.skills {
  width: 50%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25%, 1fr));
  align-items: center;
  justify-content: center;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 30px;
}
.skills div {
  text-align: center;
  width: 100%;
}

/* latest works */
.latest-works {
  display: flex;
  align-items: center;
  width: 85%;
  margin: auto;
  justify-content: space-between;
  margin-bottom: 20px;
  margin-top: 50px;
}
.latest-works-l,
.latest-works-r {
  display: flex;
  flex-direction: column;
  flex-basis: 50%;
}
.latest-works-r {
  align-items: flex-end;
}
.latest-works-l-inner,
.latest-works-r-inner {
  width: 90%;
}
.latest-works-l-inner-1,
.latest-works-r-p-2 {
  width: 100%;
  margin-bottom: 15px;
}
.latest-works-l-img-con,
.latest-works-r-p-2-img-con {
  border-radius: 15px;
}
.latest-works-l-img,
.latest-works-r-p-2-img-1,
.latest-works-r-p-2-img-2 {
  height: 200px;
  background-position: 20px 50px;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: rgb(45, 45, 45);
  backdrop-filter: blur(20px);
  width: 100%;
  border-radius: 15px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.latest-works-r-p-2-img-2-h,
.latest-works-l-img-h {
  margin-top: 10px;
}

.visit-site-link {
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.visit-site-link a {
  background-color: rgb(45, 45, 45);
  backdrop-filter: blur(20px);
  padding: 5px 15px;
}
.visit-site-link a:hover {
  color: rgb(17, 248, 17);
  font-weight: bolder;
}
.home-footer {
  margin-top: 40px;
}

/* latest */
.latest-works-o {
  width: 85%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(40%, 1fr));
  column-gap: 5rem;
}

.latest-works-o-l {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media screen and (max-width: 1024px) {
  .home-top-content-inner-r img {
    width: 70%;
    margin: auto;
  }
}

@media screen and (max-width: 900px) {
  .home-top {
    width: 70%;
    font-size: 2rem;
  }
  .home-top-content-inner-r img {
    width: 90%;
    margin: auto;
  }
}
@media screen and (max-width: 768px) {
  .home-top {
    width: 80%;
    font-size: 2rem;
  }
  /* home-top */
  .home-top-content-inner {
    width: 95%;
    flex-direction: column;
  }
  .home-top-content-inner-l {
    flex-direction: row-reverse;
  }
  .home-top-content-inner-l-1 {
    margin-left: 3%;
  }
  .home-top-content-inner-l-2 {
    align-items: flex-end;
    justify-content: flex-end;
    width: 15%;
    transform: rotate(-90deg);
  }
  .home-top-content-inner-l-1-h {
    font-size: 28px;
  }
  .home-top-content-inner-l-2-n p {
    font-weight: 400;
    font-size: 26px;
  }
  .home-top-content-inner-r {
    margin-top: 20px;
  }
  .home-top-content-inner-r img {
    width: 50%;
    margin: auto;
  }

  /* 
  =============
  ==============
  second layer
  */
  .home-second-layer {
    flex-direction: column-reverse;
  }
  .home-second-layer-l,
  .home-second-layer-r {
    width: 100%;
    margin: auto;
    margin-bottom: 20px;
  }
  .home-second-layer-l-div,
  .home-second-layer-r-div {
    width: 100%;
  }

  /* latest works */
  .latest-works {
    flex-direction: column;
  }
  .latest-works,
  .latest-works-l,
  .latest-works-r {
    width: 95%;
    margin: auto;
    justify-content: center;
    align-items: center;
  }
  .latest-works-l,
  .latest-works-r {
    width: 100%;
  }
  .latest-works-l-inner-1 span,
  .latest-works-l-inner-1 .contact-link-proj {
    display: none;
  }
  .skills {
    grid-template-columns: repeat(auto-fit, minmax(20%, 1fr));
    width: 70%;
  }

  /* latest */
  .latest-works-o {
    grid-template-columns: repeat(auto-fit, minmax(100%, 1fr));
  }
}

@media screen and (max-width: 600px) {
  .home-top {
    width: 90%;
    font-size: 1.8rem;
  }
  .skills {
    grid-template-columns: repeat(auto-fit, minmax(25%, 1fr));
    width: 80%;
  }
}
@media screen and (max-width: 480px) {
  .home-top {
    font-size: 1.5rem;
  }
  .skills {
    transform: rotate(35deg);
    margin-top: 80px;
  }
}
